
































































































































    declare global {
        interface Window{ PrismicJS: any, PrismicDOM: any }
    }

	import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	import Product from '@/components/product.vue';
	//import BookCategories from '@/components/bookCategories.vue';
	Component.registerHooks(['beforeRouteEnter']);

	@Component({components: {
		Product //, BookCategories
	}})
	export default class Devotionals extends Vue {
		PrismicDOM = window.PrismicDOM; 
		currentCategory: string = "";
		products: any = [];
		frees: any = [];
		store: any = {};
		currentProduct: any = {qty: 1};
		viewProduct: boolean = false;

		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => {vm._getStore(); vm._getProducts(); /*vm._getFree();*/});
		}

		async _getStore() {
			let api = await _Prismic();
			this.store = await api.getSingle('store');
		}

		async _getProducts() {
			var api = await _Prismic();
			var response = await api.query(
				this.currentCategory ? window.PrismicJS.Predicates.fulltext('my.product.category', this.currentCategory) :
				window.PrismicJS.Predicates.at('document.type', 'product'),
				{ fetch:['product.title', 'product.image','product.price', 'product.description' ], pageSize : 10, page : 1 }
			);
			this.products = [...this.products, ...response.results];
		}

		private quickView(p: any) {
			this.currentProduct = { ...this.currentProduct, 
				title: this.PrismicDOM.RichText.asText(p.title || p.data.title),
				image: p.data? p.data.image.url : p.image.url,
				price: p.price || p.data.price,
                description: this.PrismicDOM.RichText.asText(p.description || p.data.description)
			};
			this.viewProduct = true;
		}

		private decQuantity() {
			if(!isNaN(this.currentProduct.qty)) this.currentProduct.qty--;
			if(this.currentProduct.qty < 1) this.currentProduct.qty = 1;
		}

		private incQuantity() {
			this.currentProduct.qty++;
		}

		private addProduct() {
			this.$store.dispatch('ADD_TO_CART', { product: {...this.currentProduct }});
			this.currentProduct.qty = 1;
			this.viewProduct = false;
		}
}
